<template>
  <div>
    <h2></h2>
    <h3 id="clock" style="padding-left: 85%;">{{ clock }}</h3>
    <p>
      <button @click="weatherApi">weather</button>
       : <span id="area">{{ area }}</span> |
       : <span id="temperature">{{ temperature }}</span>
    </p>
    <table border="1">
      <tr>
        <th width="100"></th>
        <th width="100"></th>
        <th width="150"></th>
        <th width="150"></th>
      </tr>
      <tr v-for="(t, idx) in tableData" :key="idx">
        <td align="left">{{ t.name }}</td>
        <td align="left">{{ t.rank }}</td>
        <td align="right">{{ t.bizEmail }}</td>
        <td align="right">{{ t.phone }}</td>
      </tr>
    </table>
    
  </div>
</template>

<script>
import axios from 'axios'
import { ref } from '@vue/reactivity'
import { onMounted } from '@vue/runtime-core';

const default_path = "/vue-menu";
const WEATHER_API_KEY = "89df5497eff532a13168a5062e009cee";
const UNKNOWN = '(  )';

export default {
  setup() {
    let tableData = ref([]);

    onMounted(() => {
      axios.get(default_path + '/json/table.json').then((r)=>{
        tableData.value = r.data;
      }).catch((e)=>{
        console.log("error...");
        console.log(e);
      })
    });

    return { tableData }
  },
  data() {
    return {
      clock : 'AM 00:00:00',
      area: UNKNOWN,
      temperature: UNKNOWN,
    }
  },
  mounted() {
    this.getClock();
    setInterval(this.getClock, 1000);
  },
  methods: {
    getClock() {
      const date = new Date();
      const hh = String(date.getHours()-12);
      const mm = String(date.getMinutes()).padStart(2, "0");
      const ss = String(date.getSeconds()).padStart(2, "0");
      const ap = (date.getHours() > 12) ? "PM" : "AM";

      this.clock = `${ap} ${hh}:${mm}:${ss}`;
    },
    weatherApi() {
      navigator.geolocation.getCurrentPosition(position => {
        const lat = position.coords.latitude;
        const lon = position.coords.longitude;
        const weatherUrl = `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=${WEATHER_API_KEY}&units=metric`;

        axios.get(weatherUrl).then((r) => {
          this.area = r.data.name;
          this.temperature = r.data.main.temp;
        }).catch((e) => {
          console.log(e);
        });
      }, err => {
        console.log(err);
      });
    },
  },
}
</script>

